/* Checkboxes and Radios */
.form-check,
.form-radio {
  position: relative;
  display: block;
  margin-top: 15px;
  margin-bottom: 10px;
}
.form-check .form-check-label,
.form-radio .form-check-label {
  display: block;
  padding-left: 30px;
  line-height: 1.5;
}
.rtl .form-check .form-check-label,
.rtl .form-radio .form-check-label {
  padding-left: 0;
  padding-right: 30px;
}
.form-check .form-check-label input,
.form-radio .form-check-label input {
  position: absolute;
  margin-left: -20px;
  margin-top: 4px\9;
  top: 0;
  left: 0;
  z-index: 1;
  cursor: pointer;
  opacity: 0;
  filter: alpha(opacity=0);
  margin-top: 0;
}
.rtl .form-check .form-check-label input,
.rtl .form-radio .form-check-label input {
  left: auto;
  right: 0;
}

.form-check .form-check-label {
  font-size: 14px;
  line-height: 1.5;
  padding-left: 10px;
}
.form-check .form-check-label input:checked + .input-helper:before {
  background-color: #ffffff;
}
.form-check .form-check-label input:checked + .input-helper:after {
  width: 18px;
  opacity: 1;
  line-height: 17px;
  filter: alpha(opacity=100);
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  transform: scale(1);
}
.form-check .form-check-label input:disabled + .input-helper:before {
  border-color: #f2f2f2;
}
.form-check .form-check-label input:disabled:checked + .input-helper:after {
  color: #f2f2f2;
}
.form-check .form-check-label .input-helper:before {
  position: absolute;
  content: "";
  top: 50%;
  -moz-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 18px;
  height: 18px;
  border-radius: 2px;
  left: 0;
  border: 2px solid #f2f2f2;
  -webkit-transition: all;
  -o-transition: all;
  transition: all;
  transition-duration: 0s;
  -webkit-transition-duration: 250ms;
  transition-duration: 250ms;
}
.rtl .form-check .form-check-label .input-helper:before {
  left: auto;
  right: 0;
}
.form-check .form-check-label .input-helper:after {
  transition: all;
  transition-duration: 0s;
  transition-duration: 250ms;
  font-family: FontAwesome;
  opacity: 0;
  filter: alpha(opacity=0);
  -webkit-transform: scale(0);
  transform: scale(0);
  content: "\f00c";
  position: absolute;
  font-size: 11px;
  font-weight: bold;
  left: 2px;
  top: 16%;
  -webkit-transform: translateY(-14%);
  transform: translateY(-16%);
  color: #009c8a;
}
.rtl .form-check .form-check-label .input-helper:after {
  left: auto;
  right: 0;
}

