/* Icons */
.icons-list {
  border-left: 1px solid #f2f2f2;
  border-bottom: 1px solid #f2f2f2;
}
.icons-list > div {
  background: #ffffff;
  border-top: 1px solid #f2f2f2;
  border-right: 1px solid #f2f2f2;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  padding: 15px;
  font-family: "Muli", sans-serif;
  font-size: 14px;
}
.icons-list > div i {
  display: inline-block;
  font-size: 20px;
  width: 40px;
  text-align: left;
  color: #269bff;
}

/* Lists */
dl,
ol,
ul {
  padding-left: 1rem;
  font-size: 14px;
}
dl li,
ol li,
ul li {
  line-height: 1.8;
}

.list-arrow,
.list-star,
.list-ticked {
  list-style: none;
  padding: 0;
}
.list-arrow li,
.list-star li,
.list-ticked li {
  padding-left: 1.5rem;
}
.list-arrow li:before,
.list-star li:before,
.list-ticked li:before {
  font-family: "Material Design Icons";
  margin-left: -1.5rem;
  width: 1.5rem;
  margin-right: 0.5rem;
}

.list-ticked > li:before {
  content: "\F12C";
  color: #f1635f;
}

.list-arrow > li:before {
  content: "\F142";
  color: #4bb14f;
}

.list-star > li:before {
  content: "\F4CE";
  color: #feba47;
}

.bullet-line-list {
  padding-left: 30px;
  margin-bottom: 0;
  position: relative;
  list-style-type: none;
}
.rtl .bullet-line-list {
  padding-right: 0px;
}
.bullet-line-list li {
  position: relative;
  line-height: 1;
  padding-bottom: 30px;
}
.bullet-line-list li:before {
  width: 12px;
  height: 12px;
  left: -28px;
  top: 13px;
  border: 3px solid;
  margin-right: 15px;
  z-index: 2;
  background: #ffffff;
}
.bullet-line-list li:before {
  content: "";
  position: absolute;
  border-radius: 100%;
  border-color: #269bff;
}
.bullet-line-list li:after {
  content: "";
  border: 1px solid #f2f2f2;
  position: absolute;
  bottom: 0;
  left: -23px;
  height: 100%;
}
.bullet-line-list li:first-child:after {
  content: "";
  height: 80%;
}
.bullet-line-list li:last-child {
  padding-bottom: 0;
}
.bullet-line-list li:last-child:after {
  content: "";
  top: 0;
  height: 30%;
}

/* Loaders */
#pgloading {
  position: static;
}
#pgloading .bokeh {
  font-size: 100px;
  width: 1em;
  height: 1em;
  position: relative;
  margin: 0 auto;
  border-radius: 50%;
  border: 0.01em solid #f2f2f2;
  list-style: none;
  padding: 0;
}
#pgloading .bokeh li {
  position: absolute;
  width: 0.2em;
  height: 0.2em;
  border-radius: 50%;
}
#pgloading .bokeh li:nth-child(1) {
  left: 50%;
  top: 0;
  margin: 0 0 0 -0.1em;
  background: #4bb14f;
  -webkit-transform-origin: 50% 250%;
  transform-origin: 50% 250%;
  -webkit-animation: rota 1.13s linear infinite,
    opa 3.67s ease-in-out infinite alternate;
  animation: rota 1.13s linear infinite,
    opa 3.67s ease-in-out infinite alternate;
}
#pgloading .bokeh li:nth-child(2) {
  top: 50%;
  right: 0;
  margin: -0.1em 0 0;
  background: #f1635f;
  -webkit-transform-origin: -150% 50%;
  transform-origin: -150% 50%;
  -webkit-animation: rota 1.86s linear infinite,
    opa 4.29s ease-in-out infinite alternate;
  animation: rota 1.86s linear infinite,
    opa 4.29s ease-in-out infinite alternate;
}
#pgloading .bokeh li:nth-child(3) {
  left: 50%;
  bottom: 0;
  margin: 0 0 0 -0.1em;
  background: #feba47;
  -webkit-transform-origin: 50% -150%;
  transform-origin: 50% -150%;
  -webkit-animation: rota 1.45s linear infinite,
    opa 5.12s ease-in-out infinite alternate;
  animation: rota 1.45s linear infinite,
    opa 5.12s ease-in-out infinite alternate;
}
#pgloading .bokeh li:nth-child(4) {
  top: 50%;
  left: 0;
  margin: -0.1em 0 0;
  background: #269bff;
  -webkit-transform-origin: 250% 50%;
  transform-origin: 250% 50%;
  -webkit-animation: rota 1.72s linear infinite,
    opa 5.25s ease-in-out infinite alternate;
  animation: rota 1.72s linear infinite,
    opa 5.25s ease-in-out infinite alternate;
}

