/* Wizard */
.wizard {
  display: block;
  width: 100%;
  overflow: hidden;
}
.wizard a {
  outline: 0;
}
.wizard ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.wizard ul > li {
  display: block;
  padding: 0;
}
.wizard > .steps {
  position: relative;
  display: block;
  width: 100%;
}
.wizard > .steps .current-info {
  position: absolute;
  left: -999em;
}
.wizard > .steps .number {
  font-size: 14px;
}
.wizard > .steps > ul > li {
  width: 25%;
  float: left;
  text-align: center;
}
.wizard > .steps a {
  display: block;
  width: auto;
  margin: 0 0.5em 0.5em;
  padding: 1em;
  text-decoration: none;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  font-size: 14px;
  font-family: "Muli", sans-serif;
}
.wizard > .steps a:hover {
  display: block;
  width: auto;
  margin: 0 0.5em 0.5em;
  padding: 1em;
  text-decoration: none;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
.wizard > .steps a:active {
  display: block;
  width: auto;
  margin: 0 0.5em 0.5em;
  padding: 1em;
  text-decoration: none;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
.wizard > .steps .disabled a {
  background: #f7f7f9;
  color: #6c757d;
  cursor: default;
}
.wizard > .steps .disabled a:hover {
  background: #f7f7f9;
  color: #6c757d;
  cursor: default;
}
.wizard > .steps .disabled a:active {
  background: #f7f7f9 e;
  color: #6c757d;
  cursor: default;
}
.wizard > .steps .current a {
  background: #269bff;
  color: #ffffff;
  cursor: default;
}
.wizard > .steps .current a:hover {
  background: #269bff;
  color: #ffffff;
  cursor: default;
}
.wizard > .steps .current a:active {
  background: #269bff;
  color: #ffffff;
  cursor: default;
}
.wizard > .steps .done a {
  background: #8ccaff;
  color: #ffffff;
}
.wizard > .steps .done a:hover {
  background: #8ccaff;
  color: #ffffff;
}
.wizard > .steps .done a:active {
  background: #8ccaff;
  color: #ffffff;
}
.wizard > .steps .error a {
  background: #f1635f;
  color: #fff;
}
.wizard > .steps .error a:hover {
  background: #f1635f;
  color: #fff;
}
.wizard > .steps .error a:active {
  background: #f1635f;
  color: #fff;
}
.wizard > .content {
  background: #f9fbfd;
  display: block;
  margin: 0.5em;
  min-height: 24em;
  overflow: hidden;
  position: relative;
  width: auto;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
.wizard > .content > .title {
  position: absolute;
  left: -999em;
}
.wizard > .content > .body {
  float: left;
  position: absolute;
  width: 95%;
  height: 95%;
  padding: 2.5%;
}
.wizard > .content > .body ul {
  list-style: disc;
}
.wizard > .content > .body ul > li {
  display: list-item;
}
.wizard > .content > .body > iframe {
  border: 0 none;
  width: 100%;
  height: 100%;
}
.wizard > .content > .body input {
  display: block;
  border: 1px solid #f2f2f2;
}
.wizard > .content > .body input[type="checkbox"] {
  display: inline-block;
}
.wizard > .content > .body input.error {
  background: #fbe3e4;
  border: 1px solid #f9bebd;
  color: #f1635f;
}
.wizard > .content > .body label {
  display: inline-block;
  margin-bottom: 0.5em;
}
.wizard > .content > .body label.error {
  color: #f1635f;
  display: inline-block;
  margin-left: 1.5em;
}
.wizard > .actions {
  padding-top: 20px;
  position: relative;
  display: block;
  text-align: right;
  width: 100%;
}
.wizard > .actions > ul {
  display: inline-block;
  text-align: right;
}
.wizard > .actions > ul > li {
  float: left;
  margin: 0 0.5em;
}
.wizard.vertical > .steps {
  display: inline;
  float: left;
  width: 30%;
}
.wizard.vertical > .steps > ul > li {
  float: none;
  width: 100%;
  text-align: left;
}
.wizard.vertical > .content {
  display: inline;
  float: left;
  margin: 0 2.5% 0.5em;
  width: 65%;
}
.wizard.vertical > .actions {
  display: inline;
  float: right;
  margin: 0 2.5%;
  width: 95%;
}
.wizard.vertical > .actions > ul > li {
  margin: 0 0 0 1em;
}

.tabcontrol {
  display: block;
  width: 100%;
  overflow: hidden;
}
.tabcontrol a {
  outline: 0;
}
.tabcontrol ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.tabcontrol ul > li {
  display: block;
  padding: 0;
}
.tabcontrol > .steps {
  position: relative;
  display: block;
  width: 100%;
}
.tabcontrol > .steps .current-info {
  position: absolute;
  left: -999em;
}
.tabcontrol > .steps > ul {
  position: relative;
  margin: 6px 0 0;
  top: 1px;
  z-index: 1;
}
.tabcontrol > .steps > ul > li {
  float: left;
  margin: 5px 2px 0 0;
  padding: 1px;
  -webkit-border-top-left-radius: 5px;
  -webkit-border-top-right-radius: 5px;
  -moz-border-radius-topleft: 5px;
  -moz-border-radius-topright: 5px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.tabcontrol > .steps > ul > li:hover {
  background: #edecec;
  border: 1px solid #f2f2f2;
  padding: 0;
}
.tabcontrol > .steps > ul > li > a {
  color: #8ba2b5;
  display: inline-block;
  border: 0 none;
  margin: 0;
  padding: 10px 30px;
  text-decoration: none;
}
.tabcontrol > .steps > ul > li > a:hover {
  text-decoration: none;
}
.tabcontrol > .steps > ul > li.current {
  background: #ffffff;
  border: 1px solid #f2f2f2;
  border-bottom: 0 none;
  padding: 0 0 1px;
  margin-top: 0;
}
.tabcontrol > .steps > ul > li.current > a {
  padding: 15px 30px 10px;
}
.tabcontrol > .content {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 35em;
  overflow: hidden;
  border-top: 1px solid #f2f2f2;
  padding-top: 20px;
}
.tabcontrol > .content > .title {
  position: absolute;
  left: -999em;
}
.tabcontrol > .content > .body {
  float: left;
  position: absolute;
  width: 95%;
  height: 95%;
  padding: 2.5%;
}
.tabcontrol > .content > .body ul {
  list-style: disc;
}
.tabcontrol > .content > .body ul > li {
  display: list-item;
}

